import { useEffect, useState } from "react";
import { Button, Card } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import paginationFactory from "react-bootstrap-table2-paginator";
import BootstrapTable from "react-bootstrap-table-next";
import { AiFillEye } from "react-icons/ai";
import moment from "moment";
import FullScreenLoader from "../../../../../authorization/FullScreenLoader";
import { getAllPatientPresForm } from "../../../../../app/features/PatientOnlinePrescription/PatientOnlinePrescription.slice";
import OnlinePresFormModal from "./Modal";

function OnlinePrescForm(props) {
  const { getPatientData } = props;
  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState();
  const [pageNumber, setPageNumber] = useState(1);

  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const { getAllPatientOnlinePrescForm, isError, isLoading, isSuccess } =
    useSelector((state) => state?.patientOnlinePresc || []);

  const totalRecords = getAllPatientOnlinePrescForm?.totalCounts;

  const dispatch = useDispatch();

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 5,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "5",
        value: 5,
      },
    ],
  };

  useEffect(() => {
    const finalData = {
      PageNo: pageNumber,
      Size: paginateOptions.paginationSize,
      isPagination: true,
      FormStatusId: null,
      patientId: user?.userId,
    };
    dispatch(getAllPatientPresForm(finalData));
  }, [dispatch, pageNumber, user?.userId]);

  const handlePageChange = (page, sizePerPage) => {
    setPageNumber(page);
  };

  const handleShowForm = (cell) => {
    setFormData(cell);
    setShowForm(true);
  };

  const columns = [
    {
      dataField: "formId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctor",
      text: "Doctor Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
    },
    {
      dataField: "orderDate",
      text: "Order Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        return moment(cell).format("MM/DD/YYYY");
      },
    },
    {
      dataField: "formStatus",
      text: "Status",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "isPrescriptionAdded",
      text: "Prescription",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p className="m-0">
          {cell?.isPrescriptionAdded ? "Added" : "Not Added"}
        </p>
      ),
    },
    {
      dataField: "paymentStatus",
      text: "Payment Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1", width: "14rem" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 w-75 ${
            cell?.status === "InActive" ? "inactive-status" : "active-status"
          }`}
        >
          {cell?.status === "InActive" ? "Unpaid" : "Paid"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Actions",
      sort: false,

      formatter: (row, cell) => {
        return (
          <>
            <Button
              onClick={() => handleShowForm(cell)}
              className="table-action bg-transparent border-0"
            >
              <AiFillEye /> Details
            </Button>
          </>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  return (
    <>
      <Card>
        <Card.Img variant="top" className="Card-Image" />
        <Card.Body className="p-0">
          <div className="d-flex align-items-center upload_pic">
            <img
              src={
                getPatientData?.imageUrl
                  ? getPatientData?.imageUrl
                  : `https://ui-avatars.com/api/?name=${`${getPatientData?.firstName} ${getPatientData?.lastName}  `}&background=000071&color=fff`
              }
              alt=""
              srcset=""
              className="img-fluid Profile-2 position-relative test-border"
            />
          </div>

          <Card.Title className="Personal-Information mb-5 ps-3 fw-bold">
            Form History
          </Card.Title>
          <div className="mx-4">
            {isSuccess ? (
              <BootstrapTable
                columns={columns}
                data={
                  getAllPatientOnlinePrescForm?.data
                    ? getAllPatientOnlinePrescForm?.data
                    : []
                }
                keyField="id"
                id="bar"
                pagination={paginationFactory({
                  ...paginateOptions,
                  page: pageNumber,
                  totalSize: totalRecords,
                  onPageChange: handlePageChange,
                })}
                // striped
                remote
                onTableChange={() => {}}
                bordered={false}
                wrapperClasses="table-responsive"
                sort={{ dataField: "formId", order: "desc" }}
              />
            ) : isLoading ? (
              <FullScreenLoader />
            ) : isError ? (
              <p className="text-center">Network Error...</p>
            ) : null}
          </div>
        </Card.Body>
      </Card>
      <OnlinePresFormModal
        show={showForm}
        handleClose={() => setShowForm(false)}
        formData={formData}
      />
    </>
  );
}

export default OnlinePrescForm;
