import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Modal } from "react-bootstrap";

import FullScreenLoader from "../../../../../../authorization/FullScreenLoader";
import { getPatientPresForm } from "../../../../../../app/features/PatientOnlinePrescription/PatientOnlinePrescription.slice";

export default function OnlinePresFormModal({ ...rest }) {
  const { show, handleClose, formData } = rest;
  const { patientOnlinePrescForm, isSuccess, isLoading, isError } = useSelector(
    (state) => state?.patientOnlinePresc
  );

  const dispatch = useDispatch();

  useEffect(() => {
    const finalData = {
      patientId: formData?.patientId,
      formId: formData?.formId,
      patientFormStatusId: formData?.patientFormStatusId,
      patientFormAttempt: formData?.patientFormAttempt,
    };
    dispatch(getPatientPresForm(finalData));
  }, [
    dispatch,
    formData?.patientId,
    formData?.formId,
    formData?.patientFormStatusId,
    formData?.patientFormAttempt,
  ]);

  return (
    <div className="AddSlot_modal">
      <Modal size="lg" handleClose={handleClose} show={show}>
        <Modal.Header>
          <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
            Patient Prescripion Form -{" "}
            <span
              className={
                formData?.formStatus === "Accepted"
                  ? "accepted-form"
                  : "rejected-form"
              }
            >
              {formData?.formStatus}
            </span>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body
          className="p-4"
          style={{ height: "38rem", overflow: "scroll" }}
        >
          {isSuccess ? (
            <>
              {formData?.formStatus === "Rejected" ? (
               <>
                <div>
                  <h6 className="fw-bold rejected-form">Rejected Reason:</h6>
                  <p className="m-0">{formData?.rejectedReason ? formData?.rejectedReason : "N/A"}</p>
                </div>
                <hr />
               </>
              ) : ""}
               <div className="text-center">
                  <h6 className="fw-bold m-0">Form Title</h6>
                  <p className="m-0">{formData?.form}</p>
                </div>
              {patientOnlinePrescForm?.data?.length > 0 ? (
                <>
                  {patientOnlinePrescForm?.data?.map(
                    (section, sectionIndex) => (
                      <div
                        className="mb-3"
                        controlId={`formQuestion${sectionIndex}`}
                      >
                        <p className="label-primary mt-2 fw-bold">
                          <span className="fw-bold">
                            Q.{`${sectionIndex + 1}.`}{" "}
                          </span>
                          {section.question}
                        </p>
                        {section?.answerType === "Radio" ? (
                          <>
                            {section?.formAnswersList?.length > 0 ? (
                              section?.formAnswersList?.map(
                                (value, optionIndex) => (
                                  <p>
                                    <span className="fw-bold">Ans: </span>
                                    {value?.answerLabel
                                      ? value?.answerLabel
                                      : "N/A"}
                                  </p>
                                )
                              )
                            ) : (
                              <p>
                                <span className="fw-bold">Ans: </span>
                                N/A
                              </p>
                            )}
                          </>
                        ) : section?.answerType === "TextBox" ? (
                          <>
                            {section?.formAnswersList?.length > 0 ? (
                              section?.formAnswersList?.map(
                                (value, valueIndex) => (
                                  <p>
                                    <span className="fw-bold">Ans: </span>
                                    {value?.textAnswerValue?.length > 0
                                      ? value?.textAnswerValue
                                      : "N/A"}
                                  </p>
                                )
                              )
                            ) : (
                              <p>
                                <span className="fw-bold">Ans: </span>
                                N/A
                              </p>
                            )}
                          </>
                        ) : section?.answerType === "Checkbox" ? (
                          section?.formAnswersList?.length > 0 ? (
                            <div className="d-flex">
                              <span className="fw-bold me-1">Ans:</span>
                              <p className="d-inline-grid mb-0">
                                {section?.formAnswersList?.map(
                                  (option, optionIndex) => (
                                    <span key={optionIndex} className="mb-0">
                                      {section.formAnswersList.length > 1 &&
                                        `${optionIndex + 1}. `}
                                      {option?.answerLabel
                                        ? option?.answerLabel
                                        : "N/A"}
                                    </span>
                                  )
                                )}
                              </p>
                            </div>
                          ) : (
                            <p>
                              <span className="fw-bold">Ans: </span>
                              N/A
                            </p>
                          )
                        ) : null}
                      </div>
                    )
                  )}
                </>
              ) : (
                <p className="text-center mt-3">No Record Found</p>
              )}
            </>
          ) : isLoading ? (
            <FullScreenLoader />
          ) : isError ? (
            <p className="text-center mt-3">Network Error...</p>
          ) : null}
        </Modal.Body>
        <Modal.Footer className="Doctor-Modal-Footer">
          <Button
            onClick={handleClose}
            className="Admin-Modal-CancelBtn fw-bold"
          >
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
